        <p></p>
      </div>
    </div>

    <div class="container">
      <div class="row">
        <!--  -->
        <div class="col-md-6">
          <div class="certificate-card">
            <div class="card-header">
              <h2></h2>
              <p class="text-muted"></p>
            </div>
            
            <div class="card-body">
              <form @submit.prevent="submitForm">
                <div class="form-group">
                  <label for="name">/ <span class="required">*</span></label>
                  <input 
                    type="text" 
                    id="name" 
                    v-model="formData.name" 
                    class="form-control" 
                    required
                    placeholder="/"
                  >
                </div>
                
                <div class="form-group">
                  <label for="descriptionTitle"> <span class="required">*</span></label>
                  <div class="input-group">
                    <select 
                      class="form-control" 
                      v-model="selectedTitle" 
                      @change="onTitleChange"
                    >
                      <option value="">--  --</option>
