                    v-model="formData.descriptionPrefix" 
                    class="form-control" 
                    required
                    rows="2"
                  ></textarea>
                  <small class="form-text text-muted"></small>
                </div>
                
                <div class="form-group">
                  <label for="descriptionSuffix"> <span class="required">*</span></label>
                  <textarea 
                    id="descriptionSuffix" 
                    v-model="formData.descriptionSuffix" 
                    class="form-control" 
                    required
                    rows="2"
                  ></textarea>
                  <small class="form-text text-muted"></small>
                </div>

                <div class="form-group">
                  <label> <span class="required">*</span></label>
                  <div class="certificate-orientation">
                    <div class="form-check form-check-inline">
                      <input 
                        class="form-check-input" 
                        type="radio" 
                        name="orientation" 
                        id="horizontal" 
                        value="horizontal" 
                        v-model="certificateOrientation"
                      >
                      <label class="form-check-label" for="horizontal">
                        
                      </label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input 
                        class="form-check-input" 
                        type="radio" 
                        name="orientation" 
                        id="vertical" 
                        value="vertical" 
                        v-model="certificateOrientation"
                      >
                      <label class="form-check-label" for="vertical">
                        
                      </label>
                    </div>
                  </div>
                </div>
                
