<script>
import axios from 'axios';

export default {
  name: "certificate",
  data() {
    return {
      formData: {
        name: "",
        descriptionTitle: "",
        descriptionPrefix: "",
        descriptionSuffix: ""
      },
      selectedTitle: "",
      certificateOrientation: "horizontal", // 
      isSubmitting: false,
      certificateUrl: null,
      apiError: null
    };
  },
  methods: {
    onTitleChange() {
      // formData
      if (this.selectedTitle && this.selectedTitle !== 'custom') {
        this.formData.descriptionTitle = this.selectedTitle;
      } else if (this.selectedTitle === 'custom') {
        // 
        this.formData.descriptionTitle = '';
      }
    },
    
    submitForm() {
      // 
      if (!this.formData.name || !this.formData.descriptionTitle || 
          !this.formData.descriptionPrefix || !this.formData.descriptionSuffix ||
          !this.certificateOrientation) {
        this.apiError = "";
        return;
      }
      
      this.isSubmitting = true;
      this.apiError = null;
      
      // URL
      if (this.certificateUrl) {
        URL.revokeObjectURL(this.certificateUrl);
      }
      
      // 
      const today = new Date();
      const year = today.getFullYear();
      const month = today.getMonth() + 1; // 0+1
      const day = today.getDate();
      const formattedDate = `${year}${month}${day}`;
      
      // API
      const apiData = {
        title: "",
        subtitle: "CERTIFICATE OF HONOR",
        text: "",
        name: this.formData.name,
        descriptionPrefix: this.formData.descriptionPrefix,
        descriptionSuffix: this.formData.descriptionSuffix,
        descriptionTitle: this.formData.descriptionTitle,
        signatureName1: formattedDate,
        signatureTitle1: "",
        signatureName2: "CIC",
        signatureTitle2: ""
      };
      
      // ID
      const templateId = this.certificateOrientation === 'horizontal' 
        ? 'ep-KmswvuJyDYRF'  // ID
        : 'ep-Qq0aaRBjgQVS'; // ID
      
      // APIresponseTypeblob
      axios.post('https://ciczs.bestzyq.cn/', apiData, {
        headers: {
          'Content-Type': 'application/json',
          'OE-TEMPLATE-ID': templateId
        },
        responseType: 'blob' // blob
      })
      .then(response => {
        // PNG
        const blob = new Blob([response.data], { type: 'image/png' });
        
        // URL
        this.certificateUrl = URL.createObjectURL(blob);
      })
      .catch(error => {
        console.error(':', error);
        this.apiError = '';
        alert('');
      })
      .finally(() => {
        this.isSubmitting = false;
      });
    },
    
    resetForm() {
      // URL
      if (this.certificateUrl) {
        URL.revokeObjectURL(this.certificateUrl);
      }
      
      this.formData = {
        name: "",
        descriptionTitle: "",
        descriptionPrefix: "",
        descriptionSuffix: ""
      };
      this.selectedTitle = ""; // 
      this.certificateOrientation = "horizontal"; // 
      this.certificateUrl = null;
      this.apiError = null;
    },
    
    downloadCertificate() {
      if (!this.certificateUrl) return;
      
      // a
      const downloadLink = document.createElement('a');
      downloadLink.href = this.certificateUrl;
      
      // 
      const fileName = `${this.formData.name}-.png`;
      downloadLink.download = fileName;
      
      // 
      document.body.appendChild(downloadLink);
      downloadLink.click();
      
      // DOM
      document.body.removeChild(downloadLink);
    }
  },
  
  beforeDestroy() {
    // URL
    if (this.certificateUrl) {
      URL.revokeObjectURL(this.certificateUrl);
    }
  }
};
</script>

